.card {
    background: #fff;
    border-radius: 2px;
    height: 150px;
    margin: 1rem;
    position: relative;
    width: 150px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  .card:hover{
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
  }
  
  .img-container {
    height: 85%;
    /* overflow: hidden; */
    text-align: center;
    background:#6CADDC;
  }

  img {
    max-width: 85%;
    max-height: 85%;
  }
  

  


